html {
    height: 100%;
    touch-action: manipulation;
}

#root{
    height: 100%;
}

body {
    height: 100%;
    margin: 0;
}

.wrapper-container {
    width: 100%;
    height: 100%;
    background: url("./img/art_space.jpeg") no-repeat;
    background-size: cover;
    overflow: scroll;
    background-attachment: fixed;
    display: flex;
    justify-content: center;
    margin: auto;
}
h1 {
    padding-top: 20px;
    font-size: 38px;
    color: red;
    text-align:center
}

.explain {
    text-align: center;
    font-size: 15px;
    background-color:rgba(255,255,255,0.5);
    font-weight: bold;
}

p {
    color: black
}

.information{
    padding: 10px;
}

.score-message{
    padding-top:20px;
}

.board-table {
    border-collapse: separate;
    border-spacing: 0;
}

.board-table td {
    width: 25px;
    height: 25px;
    margin: 0px;
    padding: 0px;
    border: solid 1px white;
    background-color:black;
}


@media screen and (min-width:480px) { 
    /*　画面サイズが480pxからはここを読み込む　*/
    .tetris {
        width: 100%;
        height: 100%;
    }
}
@media screen and (min-width:768px) and ( max-width:1024px) {
    /*　画面サイズが768pxから1024pxまではここを読み込む　*/
    .tetris {
        width: 480px;
        height: 100%;
    }
}
@media screen and (min-width:1024px) {
    /*　画面サイズが1024pxからはここを読み込む　*/
    .tetris {
        width: 480px;
        height: 100%;
    }
}

.title {
    height: 25%;
}

.tetris-container {
    position: center;
    display: flex;
    justify-content: center;
    flex-direction: row;
    margin: 10px;
}

.tetris-panel-container {
    width: 200px;
