.button {
    text-align:center
}
.changeLanguage {
    margin: 5px 5px;
    display: inline-block;
	position: relative;
	width: 80px;
	height: 30px; 
	line-height:30px;
	text-align: center;
	text-decoration: none;
	color: #1B1B1B;
	background: #fff;
    background-color:rgba(255,255,255,0.8);
}

.changeLanguage::before {
    display: inline-block;
    content: "";
    width: 10px;
    height: 10px;
    position: absolute;
    top: 0px;
    left: 0px;
    border-top: 1px solid #1B1B1B;
    border-left: 1px solid #1B1B1B;	
}
.changeLanguage::after {
    display: inline-block;
    content: "";
    width: 10px;
    height: 10px;
    position: absolute;
    top: 0px;
    right: 0px;
    border-top: 1px solid #1B1B1B;
    border-right: 1px solid #1B1B1B;
}
.changeLanguage span::before {
    display: inline-block;
    content: "";
    width: 10px;
    height: 10px;
    position: absolute;
    bottom: 0px;
    left: 0px;
    border-bottom: 1px solid #1B1B1B;
    border-left: 1px solid #1B1B1B;
}
.changeLanguage span::after {
    display: inline-block;
    content: "";
    width: 10px;
    height: 10px;
    position: absolute;
    bottom: 0px;
    right: 0px;
    border-bottom: 1px solid #1B1B1B;
    border-right: 1px solid #1B1B1B;	
}
.changeLanguage:hover {
	 cursor: pointer;
	 text-decoration: none;
	 transition: transform .3s;
	transform-origin: center;
	transform: scale(0.8, 0.8); 
}


